import React, { PureComponent } from "react";

type UnitPosition = "left" | "right" | "top" | "bottom";
type UnitStyle = "normal" | "subscript" | "superscript";


  const renderUnitText = (text: string, style: UnitStyle) => {
  switch (style) {
    case "superscript":
      return <sup>{text}</sup>;
    case "subscript":
      return <sub>{text}</sub>;
    default:
      return <span>{text}</span>;
  }
};

class UnitOfMeasurement extends PureComponent<any> {
  render() {
    const { value, disabled, center, offsetText,uom ,mergedValueSx,mergedUnitSx ,formatted,...rest}:any = this.props;

    return (
      // <text
   
      //  transform={`translate(${center.x + offsetText},${center.y + 50})`}
      //   opacity={disabled ? 0.4 : undefined}
   
      
      //   {...rest}

  
      // >
      //  {value+" "+uom}
      // </text>

       <foreignObject
      x={center.x - 100 + offsetText}
      y={center.y - 40}
      width={200}
      height={80}
      pointerEvents="none"
      opacity={disabled ? 0.4 : undefined}
    >
      <div
        style={{
          display: "flex",
          flexDirection: "row",
            // formatted.position === "top" || formatted.position === "bottom"
            //   ? "column"
            //   : "row",
          alignItems: "center",
          justifyContent: "center",
          width: "100%",
          height: "100%",
          gap: 4,
          ...mergedValueSx,
          background:"transparent"
        }}
      >
        {/* UNIT LEFT */}
        {formatted.unitText && formatted.position === "left" && (
          <div style={mergedUnitSx}>
            {renderUnitText(formatted.unitText, formatted.unitStyle!)}
          </div>
        )}

        {/* VALUE */}
        <div style={mergedValueSx}>{formatted.text}</div>

        {/* UNIT RIGHT */}
        {formatted.unitText && formatted.position === "right" && (
          <div style={mergedUnitSx}>
            {renderUnitText(formatted.unitText, formatted.unitStyle!)}
          </div>
        )}

        {/* UNIT TOP */}
        {formatted.unitText && formatted.position === "top" && (
          <div style={{ ...mergedUnitSx, order: -1 }}>
            {renderUnitText(formatted.unitText, formatted.unitStyle!)}
          </div>
        )}

        {/* UNIT BOTTOM */}
        {formatted.unitText && formatted.position === "bottom" && (
          <div style={{ ...mergedUnitSx, order: 1 }}>
            {renderUnitText(formatted.unitText, formatted.unitStyle!)}
          </div>
        )}
      </div>
    </foreignObject>
    );
  }
}

export default UnitOfMeasurement;
